<template>
  <div>
    <div class="headline">精选专题
      <div class="goback" @click="back">
        <img src="/static/img/back.png" alt="">         
      </div>
    </div>

    <ClassifyList :productCategoryList="productCategoryList"/>                                     
  </div>
</template>

<script>
  import ClassifyList from '@/pages/classification/ClassifyList.vue'

  export default {
    components: {
      ClassifyList         
    },    
    data () {
      return { 
        id: this.$route.params.id,
        productCategoryList:[]            
      }
    }, 
    created() {
      this.getData();
    },   
    methods: {
      getData() {
        this.$axios.get('getProductCategory/' + this.$route.params.id,{
        })
        .then(res=>{
          if (res.data.code === 200) {
            this.productCategoryList = res.data.data
          } else {               
            console.log('**********code is not 200 ***********')
          }
        })
        .catch(err=>{
          this.code = 404
          console.log(err)
          console.log('**********code is 404 ***********')
        })
      },
      back(){
        this.$router.go(-1);//返回上一层
      }
    }
  }
</script>


<style  lang="scss" scoped>
  .headline {
    width: auto;
    height: 38px;
    text-align: center;
    font-size:15px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:rgba(71,76,69,1);
    line-height:38px;
    position: relative;
    background:rgba(255,255,255,1);
    border-bottom:1px solid #efeeee;
    margin-bottom: 5px;

    .goback {
      width: 15px;
      height: 15px;
      padding: 13px;
      position: absolute;
      left: -5px;
      top: 0px;
      z-index: 88;
      display: flex;
      justify-content: center;
      align-items: center;      

      img{
        width: 15px;
        height: 15px;        
      }
    }   
  }
</style>



