<template>  
  <li>
    <router-link :to="'/getOneProduct/' + item.newsid">
        <div class="box2">
          <img v-lazy="item.thumb" alt="">
        </div>
        <div class="box">
          <p>借款期限</p> 
          <div class="details">
            <div class="row-3">
              <p class="light">额度（元)</p>  
              <p class="col-red">1000-10000</p>           
            </div>   
            <div class="row-3">
              <div class="center border-both">
                <p class="light">借款期限</p>  
                <p class="col-red">3-12个月</p>              
              </div>           
            </div>
            <div class="row-3">
              <div class="center">
                <p class="light">日利率</p>  
                <p class="col-red">0.027%</p>            
              </div>  
            </div>                         
          </div>                 
        </div>
    </router-link>   
  </li>
</template>

<script>
  export default {
    props: {
      item: Object
    }
  }
</script>


<style  lang="scss" scoped>
  li{
    width:auto;
    height:84px;
    background:rgba(255,255,255,1);
    border:1px solid rgba(227, 227, 227, 1);
    border-radius:4px;
    margin: 0px 7px 5px 7px;
    padding: 0px 6px;

    a{
      width:auto;
      height:84px;      
      text-decoration: none;
      display: flex;
      align-items: center;     



      .box2{
        width: 56px;
        height: 56px;

        img{
          width: 56px;
          height: 56px;
        }
      }

      .box{
        width: 100%;
        height: 56px;
        padding-left: 8px;
        display: flex;
        flex-direction: column ;

        p{
          font-size:17px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(56,57,56,1);
          line-height:25px;
        }

        .details{
          width:auto;
          height:30px;    
          display:flex;
          flex-wrap: wrap;  
          align-items: center;  
          
          .row-3{
            flex:0 0 33.33333%;   
            
            p{
              font-size:13px;
              font-family:MicrosoftYaHei;
              font-weight:400;
              color:rgba(56,57,56,1);
              line-height:16px;
            }

            .light{
              color:rgba(137,137,137,1);
            }

            .col-red{
              color:rgba(234,64,47,1);
            }

            .center{
              display: flex;
              flex-direction: column ;
              align-items: center;
              border-left:1px solid #efeeee;
            }

            .border-both{
              border-left:1px solid #efeeee;
              border-right:1px solid #efeeee;
            }            
          }
        }
      }

    }
  }
</style>



